import {userApi} from "../../core/api";
import {userActions} from "./index";
import {APP_SET_IS_READY} from "../actionTypes";

export default {
    init: function () {
        return dispatch => {
            userApi.me().then(response => {
                dispatch(userActions.setUser(response.data));
            }).catch(error => {
                dispatch(userActions.setUser(null))
            }).finally(() => {
                dispatch(this.setAppIsready(true));
            });
        }
    },

    setAppIsready: (payload) => ({
        type: APP_SET_IS_READY,
        payload: payload
    })


}