import React from 'react'
import {orderApi} from "../../../core/api";
import {Breadcrumb, Descriptions, Badge, List, Typography, Divider, Button, Select} from 'antd';
import {LetterOrAvatar} from "../../../components";
import {EditOutlined, CarOutlined} from '@ant-design/icons';

import './OrderShow.scss'
import {Link} from "react-router-dom";

const {Title} = Typography;
const {Option} = Select;

const statusesVariants = {
    0: {
        text: ' ',
        color: 'red'
    },
    1: {
        text: '',
        color: 'green'
    }
}

const isMobileStyles = () => {
    return (/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)) ? {
        display: "flex",
        justifyContent: 'space-between',
        alignItems: 'center'
    } : {

    }
}


const ShowOrder = props => {
    const [order, setOrder] = React.useState(null);
    const [changeStatus, setChangeStatus] = React.useState(false)
    const [changeDeliveryType, setDeliveryTypes] = React.useState(false)
    const [changeStatusPaid, setChangeStatusPaid] = React.useState(false)
    const [statuses, setStatuses] = React.useState({});
    React.useEffect(() => {
        orderApi.show(props.match.params.id)
            .then(response => {
                if (null === response.data.data) {
                    props.history.push('/404')
                }
                setOrder(response.data.data);
            })
            .catch(err => {
                props.history.push('/404')
            })
    }, [])


    const setStatus = (val, type = 'status') => {
        orderApi.setStatus(order.id, {[type]: val})
            .finally(_ => {
                setOrder({
                    ...order,
                    [type]: val
                })

                // setStatuses(tempStatuses);

                setChangeStatus(false)
                setDeliveryTypes(false);
                setChangeStatusPaid(false);
            })
    }

    if (null === order) {
        return <div>

        </div>
    }

    return (
        <div className={'site-content'}>

            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/"}>
                        
                    </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>{order?.name}</Breadcrumb.Item>
            </Breadcrumb>

            <div className={"site-layout-content"}>
                <Descriptions title=" ">
                    <Descriptions.Item label="">{order.name}</Descriptions.Item>
                    <Descriptions.Item label=" ">{order.phone}</Descriptions.Item>
                    <Descriptions.Item label="">{order.address || ' '}</Descriptions.Item>
                    <Descriptions.Item label=" ">
                        <Badge style={{backgroundColor: '#52c41a'}} count={order.total_sum + ''}
                               overflowCount={1000000}/>
                    </Descriptions.Item>
                    <Descriptions.Item label=" ">
                        {order.date_delivery}
                    </Descriptions.Item>

                    <Descriptions.Item label="" style={isMobileStyles()}>

                        {false === changeStatus &&
                        <div className={"cursor-pointer"} onClick={() => setChangeStatus(true)}>
                            <Badge color={statusesVariants[order.status].color}
                                   text={statusesVariants[order.status].text}/>
                            <EditOutlined/>
                        </div>
                        }

                        {true === changeStatus && <div>
                            <Select defaultValue={order.status} style={{width: '100%'}}
                                    onChange={val => setStatus(val)}>
                                <Option value="0"> </Option>
                                <Option value="1"></Option>
                            </Select>
                        </div>}
                    </Descriptions.Item>

                    <Descriptions.Item label=" " style={isMobileStyles()}>

                        {false === changeDeliveryType &&
                        <div className={"cursor-pointer"} onClick={() => setDeliveryTypes(true)}>
                            {order.deliveryTypes[order.delivery_type]}
                            <EditOutlined/>
                        </div>
                        }

                        {true === changeDeliveryType && <div>
                            <Select defaultValue={order.delivery_type} style={{width: '100%'}}
                                    onChange={val => setStatus(val, 'delivery_type')}>
                                {order.deliveryTypes.map((item, key) => <Option key={key} value={key}>{item}</Option>)}
                            </Select>
                        </div>}
                    </Descriptions.Item>

                    <Descriptions.Item style={isMobileStyles()} label=" ">

                        {false === changeStatusPaid &&
                        <div className={"cursor-pointer"} onClick={() => setChangeStatusPaid(true)}>
                            <Badge color={statusesVariants[order.payment_status].color}
                                   text={order.paymentStatuses[order.payment_status]}/>
                            <EditOutlined/>
                        </div>
                        }

                        {true === changeStatusPaid && <div>
                            <Select defaultValue={order.payment_status} style={{width: '100%'}}
                                    onChange={val => setStatus(val, 'payment_status')}>
                                {order.paymentStatuses.map((item, key) => <Option key={key} value={key}>{item}</Option>)}
                            </Select>
                        </div>}
                    </Descriptions.Item>


                </Descriptions>

                <Divider/>

                <div>

                    <Title level={4}></Title>

                    <List
                        itemLayout="horizontal"
                        dataSource={order.foods}
                        renderItem={item => (
                            <List.Item>
                                <List.Item.Meta
                                    avatar={<LetterOrAvatar url={item.image} letter={item.letter} name={item.name}/>}
                                    title={item.name}
                                    description={<div className={"order"}>
                                        <span>{item.pivot.comment}</span>
                                        <Badge style={{backgroundColor: '#52c41a'}}
                                               count={item.pivot.count + ' ' + item.pivot.unit}
                                               overflowCount={1000000}/>
                                    </div>}
                                />
                            </List.Item>
                        )}
                    />
                </div>

                <div>
                    <Divider/>
                    <div>
                        <Button onClick={() => props.history.push(`/orders/${order.id}/taxi`, {order})} type="primary"
                                shape="circle" icon={<CarOutlined/>} size={'large'}
                                style={{backgroundColor: '#fee35d', color: '#fff'}}/>
                    </div>
                </div>

            </div>

        </div>
    )
}

export default ShowOrder