import React from "react";
import {FormContext} from "./FormProvider";
import {Select, DatePicker, TimePicker, Typography, Input, Divider, InputNumber, Button, Form, Alert} from 'antd';
import {validateField} from "../../../utils/helpers/index";

const {Option} = Select;
const {Title} = Typography;


export default () => {
    const context = React.useContext(FormContext);
    const refInput = React.useRef(null)

    React.useEffect(_ => {
        window.ref = refInput;
    }, [context.isReady])

    return (
        <Form onSubmit={context.formik.handleSubmit} className={"order-form"}>
            <Title level={4}> </Title>

            <div className={"order-form__form"}>
                <Form.Item
                    validateStatus={validateField("clientId", context.formik.touched, context.formik.errors)}
                    help={!context.formik.touched.clientId ? "" : context.formik.errors.clientId}
                    hasFeedback
                >
                    <Select
                        showSearch
                        placeholder=" "
                        optionFilterProp="children"
                        onChange={(value) => {
                            context.formik.setValues({
                                ...context.formik.values,
                                clientId: value
                            })

                            context.setSelectedClient(context.clients.find(item => item.id === value));

                        }}
                        filterOption={(input, option) =>
                            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                        }
                    >
                        {context.clients.map(client => <Option key={client.id}
                                                               value={client.id}>{client.name}</Option>)}

                    </Select>
                </Form.Item>

                <Form.Item
                    validateStatus={validateField("date_delivery", context.formik.touched, context.formik.errors)}
                    help={!context.formik.touched.date_delivery ? "" : context.formik.errors.date_delivery}
                    hasFeedback
                >
                    <DatePicker
                        style={{width: '100%'}}
                        ref={refInput}
                        placeholder={"  "}
                        onChange={(date, dateString) => context.formik.setValues({
                            ...context.formik.values,
                            date_delivery: dateString
                        })}/>
                </Form.Item>


                <Form.Item
                    validateStatus={validateField("time_delivery", context.formik.touched, context.formik.errors)}
                    help={!context.formik.touched.time_delivery ? "" : context.formik.errors.time_delivery}
                    hasFeedback
                >
                    <TimePicker
                        style={{width: '100%'}}
                        format={"HH:mm"}
                        onChange={(date, dateString) => context.formik.setValues({
                            ...context.formik.values,
                            time_delivery: dateString
                        })}
                    />
                </Form.Item>

            </div>

            {
                context.selectedClient
                &&
                <div>
                    <Divider/>

                    <Title level={4}>  </Title>

                    <div className={"order-form__form"}>
                        <Input placeholder={""} value={context.selectedClient.name}/>
                        <Input placeholder={""} style={{marginTop: 5}} value={context.selectedClient.phone}/>
                        <Input placeholder={" "} style={{marginTop: 5}} value={context.selectedClient.address}/>
                    </div>
                </div>
            }

            <div>
                <Divider/>
                <Title level={4}></Title>

                {context.errors.map(error => {
                    return (
                        <Alert message={error} type="error" showIcon/>
                    )
                })}

                {context.cart.map((cartItem, key) => {
                    return (
                        <React.Fragment key={key}>
                            <Divider orientation={"left"}>{key + 1}</Divider>
                            <div className={"order-form__form"}>
                                <Select
                                    showSearch
                                    placeholder=" "
                                    optionFilterProp="children"
                                    onChange={(value) => {
                                        context.handleChangeFood(value, key, 'foodID')

                                    }}
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                >
                                    {context.foods.map(food => <Option key={food.id}
                                                                       value={food.id}>{food.name}</Option>)}

                                </Select>

                                <div style={{display: 'flex', marginTop: 5}}>
                                    <InputNumber placeholder={"-"}
                                                 value={cartItem.count}
                                                 onChange={value => context.handleChangeFood(value, key, 'count')}
                                                 style={{width: "100%"}}/>

                                    <Select defaultValue={cartItem.unit} style={{width: "100%"}} onChange={value => {
                                        context.handleChangeFood(value, key, 'unit')
                                    }}>
                                        <Option value=""></Option>
                                        <Option value=""></Option>
                                    </Select>
                                </div>

                                <Input style={{marginTop: 5}} placeholder={""} value={cartItem.comment}
                                       onChange={e => context.handleChangeFood(e.target.value, key, 'comment')}/>

                            </div>
                        </React.Fragment>
                    )
                })}

                <Button style={{marginTop: 5}} onClick={context.pushItem}>+</Button>


            </div>


        </Form>
    )
}