import React from "react";
import {Breadcrumb, Form, Input, notification } from "antd";
import {PhoneOutlined, UserOutlined, HomeOutlined} from '@ant-design/icons';

import {useFormik} from "formik";
import {Button, Block} from "components";
import {validateField} from "utils/helpers";
import {clientApi} from "../../../core/api";
import apiClient from "../../../core/api/client";
import {Link} from "react-router-dom";


const validate = values => {
    const errors = {};

    const rules = {
        name: value => {
            if (!value) {
                errors.name = "   ";
            }
        },

        phone: value => {
            if (!value) {
                errors.phone = "  ";
            }
        }
    }

    Object.keys(values).forEach(key => rules[key] && rules[key](values[key]));

    return errors;
};


const openNotificationWithIcon = type => {
    notification[type]({
        message: '',
        description:
            '  ',
    });
};

const ClientForm = props => {

    const formik = useFormik({
        initialValues: {
            name: '',
            phone: '',
            address: '',
            id: '',
        },
        validate,
        onSubmit: values => {
            apiClient.save(values).then(response => {
                openNotificationWithIcon('success')
                props.history.push(`/clients/${response.data.client.id}`)
            })
        },
    });


    React.useEffect(() => {
        if (undefined !== props.location.state) {
            formik.setValues(props.location.state.client)
        } else if (props.match.params.hasOwnProperty('id')) {
            clientApi.show(props.match.params.id)
                .then(response => {
                    formik.setValues(response.data.client)
                })
        }

    }, [props.location.state, props.match.params])

    return (
        <div>


            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/clients"}>
                        
                    </Link>
                </Breadcrumb.Item>
                {
                    formik.values.id !== ''
                        ?
                        <React.Fragment>
                            <Breadcrumb.Item>
                                <Link to={"/clients/" + formik.values.id}>
                                    {formik.values.name}
                                </Link>
                            </Breadcrumb.Item>
                            <Breadcrumb.Item></Breadcrumb.Item>
                        </React.Fragment>
                        :
                        <Breadcrumb.Item></Breadcrumb.Item>

                }

            </Breadcrumb>

            <Block>
                {formik.values &&
                <Form onSubmit={formik.handleSubmit} className="client-form">
                    <Form.Item
                        validateStatus={validateField("name", formik.touched, formik.errors)}
                        help={!formik.touched.name ? "" : formik.errors.name}
                        hasFeedback
                    >
                        <Input
                            id="name"
                            prefix={<UserOutlined type="user" style={{color: "rgba(0,0,0,.25)"}}/>}
                            size="medium"
                            placeholder=""
                            value={formik.values.name}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                    </Form.Item>


                    <Form.Item
                        validateStatus={validateField("phone", formik.touched, formik.errors)}
                        help={!formik.touched.phone ? "" : formik.errors.phone}
                        hasFeedback
                    >
                        <Input
                            id="phone"
                            prefix={<PhoneOutlined type="phone" style={{color: "rgba(0,0,0,.25)"}}/>}
                            size="medium"
                            placeholder="+7(999)999-99-99"
                            value={formik.values.phone}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                    </Form.Item>

                    <Form.Item
                        validateStatus={validateField("address", formik.touched, formik.errors)}
                        help={!formik.touched.address ? "" : formik.errors.address}
                        hasFeedback
                    >
                        <Input
                            id="address"
                            prefix={<HomeOutlined type="home" style={{color: "rgba(0,0,0,.25)"}}/>}
                            size="medium"
                            placeholder=" "
                            value={formik.values.address}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                    </Form.Item>


                    <Form.Item>
                        {formik.isSubmitting && !formik.isValid && <span>!</span>}
                        <Button
                            disabled={formik.isSubmitting}
                            onClick={formik.handleSubmit}
                            type="primary"
                            size="medium"
                        >
                            
                        </Button>
                    </Form.Item>
                </Form>}
            </Block>
        </div>
    );
};

export default ClientForm;