import React from "react";
import {
    PieChartOutlined,
    UserOutlined,
    ShopOutlined,
    LoginOutlined,
    OrderedListOutlined
} from '@ant-design/icons'

import './BottomNav.scss';
import {Link} from "react-router-dom";
import {userApi} from "../../core/api";

export default () => {


    return (
        <div className="navbar">
            <Link to="/" className={window.location.pathname === '/' || window.location.pathname.includes('orders')  ? 'active' : ''}>
                <OrderedListOutlined/>
                <span> </span>
            </Link>
            <Link to="/clients" className={window.location.pathname.includes('clients') ? 'active' : ''}>
                <UserOutlined/>
                <span> </span>
            </Link>

            <Link to="/foods" className={window.location.pathname.includes('foods') ? 'active' : ''}>
                <ShopOutlined/>
                <span> </span>
            </Link>

            <Link to="/statistics" className={window.location.pathname.includes('statistics') ? 'active' : ''}>
                <PieChartOutlined/>
                <span> </span>
            </Link>

            <Link to="/#" onClick={e => {
                if (window.confirm('     ?')) {
                    userApi.logout();
                }
            }}>
                <LoginOutlined/>
                <span> </span>
            </Link>
        </div>
    )
}

