import React from 'react';
import {List, Skeleton, Breadcrumb, Divider, Button, Empty} from 'antd';
import {LetterOrAvatar} from "../../components";
import {foodApi} from "../../core/api";
import {Link} from "react-router-dom";

import './Foods.scss';

const mockData = Array(5)
    .fill(0)
    .map((item, key) => ({
        name: '',
        letter: '',
        id: key,
        loading: true,
    }))

const Foods = (props) => {

    const [data, setData] = React.useState([{
        key: '',
        data: mockData
    }]);
    React.useEffect(() => {
        foodApi.get()
            .then(response => {
                setData(response.data.data);
            })
            .catch(error => {

            })

        return () => {
            foodApi.cancel();
        }
    }, [])

    return (
        <div className={"site-content"}>
            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>
            <div className="site-layout-content">
                <div className={"foods"}>
                    <div>
                        <Button onClick={() => props.history.push('/foods/create')}>
                            
                        </Button>
                    </div>
                </div>
                {data.map((item, key) => {
                    return (
                        <div key={key}>
                            <Divider orientation={"left"}>{item.key}</Divider>
                            {item.data.map(dataItem => {
                                return (
                                    <List.Item
                                        className={'cursor-pointer'}
                                        key={dataItem.id}
                                        onClick={() => props.history.push(`/foods/${dataItem.id}`)}
                                        actions={[<Link onClick={(e) => e.stopPropagation()}
                                                        to={`/foods/${dataItem.id}/edit`}
                                                        key="list-loadmore-edit"></Link>]}
                                    >
                                        <Skeleton avatar title={false} loading={dataItem.loading} active>
                                            <List.Item.Meta
                                                avatar={
                                                    <LetterOrAvatar url={dataItem.img} letter={dataItem.letter}
                                                                    name={dataItem.name}/>
                                                }
                                                title={<Link to={`/foods/${dataItem.id}`}>{dataItem.name}</Link>}
                                                description=""
                                            />

                                        </Skeleton>
                                    </List.Item>
                                )
                            })}
                        </div>
                    )
                })}

                {data.length === 0 && <Empty
                    image="https://gw.alipayobjects.com/zos/antfincdn/ZHrcdLPrvN/empty.svg"
                    imageStyle={{
                        height: 60,
                    }}
                    description={
                        <span>
                          
                         </span>
                    }
                >
                    <Button onClick={() => props.history.push('/foods/create')} type="primary"></Button>
                </Empty>}

            </div>
        </div>
    )
}

export default Foods;