import React from 'react';
import axios from 'axios';
import {AutoComplete, Form} from 'antd';

export default ({item, callback}) => {
    const [data, setData] = React.useState([]);
    const [myTimeout, setMyTimeout] = React.useState(null)
    const [value, setValue] = React.useState('');

    React.useEffect(() => {
        if (item) {
            setValue(item.address)
            get(item.address);
        }
    }, [item]);

    const handleSearchFrom = (text) => {
        setValue(text)
        if ("" !== text) {
            get(text);
        }
    }


    const handleSelect = option => {
        const item = data.find(item => item.value === option)
        callback(item);
    }

    const get = text => {
        if (myTimeout) {
            clearTimeout(myTimeout);
        }
        const temp = setTimeout(() => {
            setData([]);
            const config = {
                "headers": {
                    "Sec-Fetch-Dest": "empty",
                    "Sec-Fetch-Mod": "cors",
                    "Sec-Fetch-Site": "same-origin",
                    "X-Site-VtmId": "134",
                    "X-Site-VtmGroupId": "56",
                    "Accept": "*/*"
                },
            }
            axios.defaults.withCredentials = false;
            axios.get('https://websites.platform.taximaster.ru/v2/find_address?needle=' + text, {headers: config.headers})
                .then(response => {
                    const items = []

                    response.data.map((responseItem, idx) => {
                        if (idx <= 9) {
                            items.push(responseItem)
                        }

                    })

                    setData(items);
                })
                .catch(error => {
                    console.log(error.response)
                })
                .finally(() => {
                })
        }, 500)

        setMyTimeout(temp)

    }

    return (
        <div>
           <Form.Item validateStatus={data.length !== 0 ? 'success' : 'error'} help={data.length !== 0 ? '' : '    '}
                      hasFeedback>
               <AutoComplete  value={value} style={{width: '100%'}} onChange={val => setValue(val)}
                             onSearch={handleSearchFrom} options={data} onSelect={handleSelect}/>
           </Form.Item>
        </div>
    )
}