import React from "react";
import _ from 'lodash';
import {clientApi, foodApi, orderApi} from "../../../core/api";
import {useFormik} from "formik";

export const FormContext = React.createContext({});


const validate = values => {
    const errors = {};

    const rules = {
        clientId: value => {
            if (!value) {
                errors.clientId = " ";
            }
        },

        date_delivery: value => {
            if (!value) {
                errors.date_delivery = "  ";
            }
        },

        time_delivery: value => {
            if (!value) {
                errors.time_delivery = "  ";
            }
        },
    }

    Object.keys(values).forEach(key => rules[key] && rules[key](values[key]));

    return errors;
};


export const FormProvider = ({children}) => {
    const [cart, setCart] = React.useState([{
        foodID: null,
        count: "1",
        unit: "",
        comment: "",
    }]);

    const [sum, setSum] = React.useState(0);
    const [order, setOrder] = React.useState(null);
    const [foods, setFoods] = React.useState([]);
    const [clients, setClients] = React.useState([]);

    const [isReady, setIsReady] = React.useState(false);
    const [selectedClient, setSelectedClient] = React.useState(null);
    const [errors, setErrors] = React.useState([]);
    const formik = useFormik({
        initialValues: {
            clientId: '',
            date_delivery: '',
            time_delivery: '',
        },
        validate,
        onSubmit: values => {
            handleSubmit();
        },
    })


    const handleChange = (value, key, field) => {
        let tempSum = 0;
        const tempCart = _.cloneDeep(cart);
        tempCart[key][field] = value || null;

        setCart(tempCart);
        tempCart.map(cartItem => {
            const priceItem = _.find(foods, function (o) {
                return o.id === cartItem.foodID
            })

            if (priceItem) {
                tempSum += priceItem.price * parseInt(cartItem.count) || 0;
            }

        });

        setSum(tempSum);
    };


    React.useEffect(() => {
        Promise.all(
            [
                clientApi.getList()
                    .then(response => {
                        setClients(response.data);
                    }),

                foodApi.getList()
                    .then(response => {
                        setFoods(response.data);
                    })
            ]
        )
            .finally(_ => {
                setIsReady(true);
            })
    }, [])


    const handleChangeFood = (value, key, field) => {
        let tempSum = 0;
        const tempCart = _.cloneDeep(cart);
        tempCart[key][field] = value || null;

        setCart(tempCart);
        tempCart.map(cartItem => {
            const priceItem = _.find(foods, function (o) {
                return o.id === cartItem.foodID
            })

            if (priceItem) {
                tempSum += priceItem.price * parseInt(cartItem.count) || 0;
            }

        });

        setSum(tempSum);
    };

    const pushItem = () => {
        let tempCart = _.cloneDeep(cart);
        tempCart.push({
            foodID: null,
            count: "1",
            unit: "",
            comment: "",
        });

        setCart(tempCart);

    };

    const handleSubmit = () => {
        setErrors([]);

        let tempCart = _.cloneDeep(cart);

        if (tempCart.length !== 1) {
            setCart(tempCart.splice(1, 1));
        }


        console.log(selectedClient);
        console.log(cart);

        const data = {
            cart: tempCart.filter(item => item.foodID !== null),
            client_id: formik.values.clientId,
            date_delivery: [formik.values.date_delivery, formik.values.time_delivery].join(' '),
            comment: '',
            ...selectedClient,
            total_sum: sum,

        };

        orderApi.save(data)
            .then(response => {
                setOrder(response.data);
            })
            .catch(error => {
                if (error.response.data.errors.hasOwnProperty('cart')) {
                    setErrors(Object.values(error.response.data.errors.cart));
                }

            });
    }

    return (
        <FormContext.Provider
            value={{
                handleChange,
                foods,
                clients,
                isReady,
                selectedClient,
                setSelectedClient,
                handleChangeFood,
                cart,
                pushItem,
                handleSubmit,
                sum,
                formik,
                errors,
                order
            }}
        >
            {children}
        </FormContext.Provider>
    );
};
