import React from "react";
import {foodApi} from "../../core/api";
import {Skeleton, Card, Breadcrumb, Modal} from 'antd';
import {EditOutlined, DeleteOutlined, ExclamationCircleOutlined} from '@ant-design/icons';
import {LetterOrAvatar} from "../../components";
import {Link} from "react-router-dom";

const {Meta} = Card;


const {confirm} = Modal;


const Food = (props) => {
    const [food, setFood] = React.useState({
        name: ''
    });

    const showDeleteConfirm = id => {
        confirm({
            title: '',
            icon: <ExclamationCircleOutlined/>,
            content: '    ?',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                foodApi.delete(id).then(response => {
                    props.history.replace('/foods');
                })
            },
            onCancel() {

            },
        });
    }


    const [loading, setLoading] = React.useState(false);

    React.useEffect(() => {
        setLoading(true)
        foodApi.show(props.match.params.id)
            .then(response => {
                if (null === response.data.food) {
                    props.history.push('/404')
                }

                setFood(response.data.food);
            })
            .catch(error => {
                console.log(error)
                props.history.push('/404')
            })
            .finally(() => {
                setLoading(false)
            })

    }, [props.match.params.id])


    return (
        <div>
            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/foods"}></Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>{food.name}</Breadcrumb.Item>
            </Breadcrumb>


            <Card
                style={{width: '100%', marginTop: 16}}
                actions={[
                    <EditOutlined key="edit"
                                  onClick={() => props.history.push(`/foods/${props.match.params.id}/edit`, {
                                      food: food
                                  })}/>,
                    <DeleteOutlined onClick={() => showDeleteConfirm(props.match.params.id)} key="delete"/>,
                ]}
            >
                <Skeleton loading={loading} avatar active>
                    <Meta
                        avatar={
                            <LetterOrAvatar url={food.image} letter={food?.letter || ''} name={food.name || ''}/>
                        }
                        title={food.name}
                    />
                </Skeleton>
            </Card>


        </div>

    )
}

export default Food;