import React from "react";
import {Breadcrumb, Form, Input, notification, Upload} from "antd";
import {BookOutlined, DollarOutlined, UploadOutlined} from '@ant-design/icons';

import {useFormik} from "formik";
import {Button, Block} from "components";
import {validateField} from "utils/helpers";
import {foodApi} from "../../../core/api";
import {Link} from "react-router-dom";


const validate = values => {
    const errors = {};

    const rules = {
        name: value => {
            if (!value) {
                errors.name = " ";
            }
        },

        price: value => {
            if (!value) {
                errors.price = " ";
            }
        }
    }

    Object.keys(values).forEach(key => rules[key] && rules[key](values[key]));

    return errors;
};


const openNotificationWithIcon = type => {
    notification[type]({
        message: '',
        description:
            '  ',
    });
};

const FoodForm = props => {
    const [img, setImg] = React.useState(null);
    const formik = useFormik({
        initialValues: {
            name: '',
            price: '',
            id: '',
        },
        validate,
        onSubmit: values => {
            foodApi.save({
                ...values,
                image: img
            }).then(response => {
                openNotificationWithIcon('success')
                props.history.push(`/foods/${response.data.food.id}`)
            })
        },
    });


    React.useEffect(() => {
        if (undefined !== props.location.state) {
            formik.setValues(props.location.state.food)
        } else if (props.match.params.hasOwnProperty('id')) {
            foodApi.show(props.match.params.id)
                .then(response => {
                    formik.setValues(response.data.food)
                })
        }
    }, [props.location.state, props.match.params])


    const fileProps = {
        beforeUpload: function (file) {
            setImg(file)


            // Prevent upload
            return false;
        },
        action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
        onChange({file, fileList}) {

        },
        defaultFileList: [],
        showUploadList: {
            // showDownloadIcon: true,
            // downloadIcon: 'download ',
            // showRemoveIcon: true,
            // removeIcon: <StarOutlined onClick={e => console.log(e, 'custom removeIcon event')} />,
        },
    };


    return (
        <div>


            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/foods"}>
                        
                    </Link>
                </Breadcrumb.Item>
                {
                    formik.values.id !== ''
                        ?
                        <React.Fragment>
                            <Breadcrumb.Item>
                                <Link to={"/foods/" + formik.values.id}>
                                    {formik.values.name}
                                </Link>
                            </Breadcrumb.Item>
                            <Breadcrumb.Item></Breadcrumb.Item>
                        </React.Fragment>
                        :
                        <Breadcrumb.Item></Breadcrumb.Item>

                }

            </Breadcrumb>

            <Block>
                {formik.values &&
                <Form onSubmit={formik.handleSubmit} className="food-form">
                    <Form.Item
                        validateStatus={validateField("name", formik.touched, formik.errors)}
                        help={!formik.touched.name ? "" : formik.errors.name}
                        hasFeedback
                    >
                        <Input
                            id="name"
                            prefix={<BookOutlined type="book" style={{color: "rgba(0,0,0,.25)"}}/>}
                            size="medium"
                            placeholder=""
                            value={formik.values.name}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                    </Form.Item>


                    <Form.Item
                        validateStatus={validateField("price", formik.touched, formik.errors)}
                        help={!formik.touched.price ? "" : formik.errors.price}
                        hasFeedback
                    >
                        <Input
                            id="price"
                            type={"number"}
                            prefix={<DollarOutlined type="price" style={{color: "rgba(0,0,0,.25)"}}/>}
                            size="medium"
                            placeholder=""
                            value={formik.values.price}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                    </Form.Item>


                    <Upload {...fileProps}>
                        <Button style={{marginBottom: 10}}>
                            <UploadOutlined/> 
                        </Button>
                    </Upload>


                    <Form.Item>
                        {formik.isSubmitting && !formik.isValid && <span>!</span>}
                        <Button
                            disabled={formik.isSubmitting}
                            onClick={formik.handleSubmit}
                            type="primary"
                            size="medium"
                        >
                            
                        </Button>
                    </Form.Item>
                </Form>}
            </Block>
        </div>
    );
};

export default FoodForm;