import React from "react";
import {Route} from "react-router-dom";

import LoginFormContainer from "./components/Login/containers/LoginFormContainer";
import RegisterFormContainer from "./components/Signup/containers/RegisterFormContainer";

import "./Auth.scss";


const Auth = () => (
    <section className="auth">
        <div className="auth__content">
            <Route exact path="/login" component={LoginFormContainer}/>
            <Route exact path="/signup" component={RegisterFormContainer}/>
        </div>
    </section>
);

export default Auth;