import React from "react";
import {Badge, Breadcrumb, List, Skeleton, Tabs} from 'antd';
import {UserOutlined, ShopOutlined} from '@ant-design/icons';
import {Link} from "react-router-dom";
import {orderApi} from "../../core/api";
import {LetterOrAvatar} from "../../components";

const {TabPane} = Tabs;


export default props => {
    const [data, setData] = React.useState([])
    const [type, setType] = React.useState('foods');
    React.useEffect(_ => {
        orderApi.getStatistic({}, type)
            .then(r => {
                setData(r.data)
            })
    }, [type])

    return (
        <div className={"site-content"}>

            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/"}>
                        
                    </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>

            <div className={"site-layout-content"}>
                <Tabs defaultActiveKey="1">
                    <TabPane
                        tab={
                            <span onClick={() => setType('foods')}>
                             <ShopOutlined/>
                              
                            </span>
                        }
                        key="1"
                    >
                        {data.map((item, key) => {
                            return (
                                <List.Item
                                    className={'cursor-pointer'}
                                    key={key}
                                >
                                    <Skeleton avatar title={false} loading={item.loading} active>
                                        <List.Item.Meta
                                            avatar={
                                                <LetterOrAvatar letter={item.letter} name={item.name}/>
                                            }
                                            title={<div style={{display: "flex", justifyContent: 'space-between', alignItems: "center"}}>
                                                <Link to={`/orders/${item.id}`}>{item.name}</Link>
                                            </div>}
                                            description={": " + item.count}
                                        />

                                    </Skeleton>
                                </List.Item>
                            )
                        })}
                    </TabPane>
                    <TabPane
                        tab={
                            <span onClick={() => setType('clients')}>
                               <UserOutlined/>
                              
                            </span>
                        }
                        key="2"
                    >
                        {data.map((item, key) => {
                            return (
                                <List.Item
                                    key={key}
                                >
                                    <Skeleton avatar title={false} loading={item.loading} active>
                                        <List.Item.Meta
                                            avatar={
                                                <LetterOrAvatar letter={item.letter} name={item.name}/>
                                            }
                                            title={<div style={{display: "flex", justifyContent: 'space-between', alignItems: "center"}}>
                                                <Link to={`/orders/${item.id}`}>{item.name}</Link>
                                                <Badge color={"#83e246"}
                                                       text={item.total + ""}/>
                                            </div>}
                                            description={": " + item.count}
                                        />

                                    </Skeleton>
                                </List.Item>
                            )
                        })}
                    </TabPane>
                </Tabs>
            </div>
        </div>
    )
}