import axios from "../config/axios";

axios.defaults.withCredentials = true;
export default {
    register: (data) => {
        return csrf().then(response => {
            return axios.post('/register', {...data, remember: 'on'})
        })
    },

    login: (data) => {
        return csrf().then(response => {
            return axios.post('/login', {...data, remember: 'on'})
        })

    },

    me: () => {
        return csrf().then(response => {
            return axios.get('/api/user')
        })
    },

    logout: () => {
        csrf().then(r => {
            axios.post('/logout')
                .then(response => {
                    window.location.reload();
                })
                .catch(error => {
                    console.log(error.response);
                })
        })
    }

}

function csrf() {
    axios.defaults.withCredentials = true;
    return axios.get('/sanctum/csrf-cookie')
}