import React from 'react';
import {List, Skeleton, Breadcrumb, Divider, DatePicker, Button, Badge, Empty} from 'antd';
import {LetterOrAvatar} from "../../components";
import {orderApi} from "../../core/api";

import './Orders.scss'
import {Link} from "react-router-dom";


const statuses = {
    0: {
        text: ' ',
        color: 'red'
    },
    1: {
        text: '',
        color: 'green'
    },

    2: {
        text: '',
        color: 'green'
    }
}

const mockData = Array(5)
    .fill(0)
    .map((item, key) => ({
        name: '',
        letter: '',
        id: key,
        loading: true,
        status: 0,
    }))

//
const Orders = (props) => {

    const [orders, setOrders] = React.useState([{
        key: '',
        data: mockData
    }]);
    const [date, setDate] = React.useState(new Date().toJSON().split("T")[0]);
    React.useEffect(() => {
        orderApi.get({date_from: date})
            .then(response => {
                setOrders(response.data);
            })
            .catch(error => {

            })
    }, [date])


    return (
        <div className={"site-content"}>
            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>
            <div className="site-layout-content">

                <div className={"orders"}>
                    <DatePicker placeholder={" "} onChange={(date, dateString) => setDate(dateString)}/>

                    <Button onClick={() => props.history.push('/orders/create')}>
                        
                    </Button>

                </div>


                {orders.map((item, key) => {
                    return (
                        <div key={key}>
                            <Divider>{item.key}</Divider>
                            {item.data.map(orderItem => {
                                return (
                                    <List.Item
                                        className={'cursor-pointer'}
                                        onClick={() => props.history.push('/orders/' + orderItem.id)}
                                        key={orderItem.id}
                                    >
                                        <Skeleton avatar title={false} loading={orderItem.loading} active>
                                            <List.Item.Meta
                                                avatar={
                                                    <LetterOrAvatar letter={orderItem.letter} name={orderItem.name}/>
                                                }
                                                title={<Link to={`/orders/${orderItem.id}`}>{orderItem.name}</Link>}
                                                description={<Badge color={statuses[orderItem.status].color}
                                                                    text={statuses[orderItem.status].text}/>}
                                            />

                                        </Skeleton>
                                    </List.Item>
                                )
                            })}
                        </div>
                    )
                })}

                {orders.length === 0 && <Empty
                    image="https://gw.alipayobjects.com/zos/antfincdn/ZHrcdLPrvN/empty.svg"
                    imageStyle={{
                        height: 60,
                    }}
                    description={
                        <span>
                             
                        </span>
                    }
                >
                    <Button onClick={() => props.history.push('/orders/create')} type="primary"></Button>
                </Empty>}

            </div>
        </div>
    )
}

export default Orders;