import axios from '../config/axios';

const CancelToken = axios.CancelToken;

let cancel;
const apiClient = {
    save: (data) => {
        if (null !== data.id && "" !== data.id) {
            return axios.put(`/api/clients/${data.id}`, data);
        }


        return axios.post('/api/clients', data);
    },

    get: (params = null) => {
        return csrf().then(response => {
            return axios.get('/api/clients', {
                params,
                cancelToken: new CancelToken(function executor(c) {
                    // An executor function receives a cancel function as a parameter
                    cancel = c;
                })
            })
        })
    },

    getList: (params = null) => {
        return axios.get('/api/clients/list', params)
    },

    show: async (id) => {
        return await axios.get(`/api/clients/${id}`);
    },

    delete: id => axios.delete(`/api/clients/${id}`),

    cancel: function () {
        cancel();
    }

}

function csrf() {
    axios.defaults.withCredentials = true;
    return axios.get('/sanctum/csrf-cookie')
}

export default apiClient;