import React from "react";
import {Layout, Menu} from "antd";
import {userApi} from "../../core/api";

import {
    PieChartOutlined,
    UserOutlined,
    ShopOutlined,
    LoginOutlined,
    OrderedListOutlined
} from '@ant-design/icons'
import {useHistory} from "react-router-dom";

const {Sider} = Layout;


const defaultSelected = () => {
    if (window.location.pathname === '/' || window.location.pathname.includes('orders')) {
        return "1"
    } else if (window.location.pathname.includes('clients')) {
        return "2";
    } else if (window.location.pathname.includes('foods')) {
        return "3";
    } else if (window.location.pathname.includes('statistics')) {
        return "4";
    }
}

export default () => {
    const [collapsed, setCollaps] = React.useState(false);
    const history = useHistory();


    return (
        <Sider className={"sider"} collapsible collapsed={collapsed} onCollapse={() => setCollaps(!collapsed)}>
            <div className="logo"/>
            <Menu theme="dark" selectedKeys={[defaultSelected()]} defaultSelectedKeys={[defaultSelected()]} mode="inline">
                <Menu.Item key="1" onClick={() => history.push('/')}>
                    <OrderedListOutlined/>
                    <span></span>
                </Menu.Item>
                <Menu.Item key="2" onClick={() => history.push('/clients')}>
                    <UserOutlined/>
                    <span></span>
                </Menu.Item>

                <Menu.Item key="3" onClick={() => history.push('/foods')}>
                    <ShopOutlined/>
                    <span></span>
                </Menu.Item>

                <Menu.Item key="4" onClick={() => history.push('/statistics')}>
                    <PieChartOutlined/>
                    <span></span>
                </Menu.Item>

                <Menu.Item key="5" onClick={() => {
                    if (window.confirm('     ?')) {
                        userApi.logout();
                    }
                }}>
                    <LoginOutlined/>
                    <span></span>
                </Menu.Item>

            </Menu>
        </Sider>
    )
}