import React from "react";
import {SearchAddress} from "../../../components";
import axios from 'axios'
import {Form, Typography, Button, Input, Breadcrumb, notification} from 'antd'
import {Link} from "react-router-dom";
import {orderApi} from "../../../core/api";
const Taxi = (props) => {

    const [from, setFrom] = React.useState({
        address: '',
    });
    const [to, setTo] = React.useState({
        address: props.location.state.order.address
    });
    const [phone, setPhone] = React.useState('+7(___)___-____')

    const [coast, setCoast] = React.useState(null);
    const [comment, setComment] = React.useState(` : ${props.location.state.order.phone}
: ${props.location.state.order.name}
        `);

    const callback = React.useCallback(() => {
        var input = document.querySelector("#phone");
        input.addEventListener("input", mask, false)
    })

    React.useEffect(() => {
        const phoneFromStorage = localStorage.getItem('phone');

        if (phoneFromStorage) {
            setPhone(phoneFromStorage)
        }
        const itemFromFromStorage = localStorage.getItem('from');

        if (itemFromFromStorage) {
            const json = JSON.parse(itemFromFromStorage);

            if (from.address === '') {
                setFrom(json);
            }


        }

        orderCoast();
        callback();
    }, [from, to])

    const orderCoast = () => {
        if (true === (from === null || to === null)) {
            return
        }
        const formData = new FormData;
        formData.append('source_lat', from.lat)
        formData.append('source_lon', from.lon)
        formData.append('destination_lat', to.lat)
        formData.append('destination_lon', to.lon)
        formData.append('source_time_iso8601', new Date().toISOString())
        axios.defaults.withCredentials = false;
        axios.post("https://websites.platform.taximaster.ru/v2/calc_order_cost", formData, {
            "headers": {
                "Sec-Fetch-Dest": "empty",
                "Sec-Fetch-Mod": "cors",
                "Sec-Fetch-Site": "same-origin",
                "X-Site-VtmId": "134",
                "X-Site-VtmGroupId": "56",
                "Accept": "*/*"
            },
        })
            .then(response => {

                setCoast(response.data)
            })
    };

    const selectFrom = item => {

        if (false === checkAddress(item.address, from.address || '')) {
            const json = JSON.stringify(item)
            localStorage.setItem('from', json);
            setFrom(item);

            console.log(item)
        }

    }

    const selectTo = item => {
        if (false === checkAddress(item.address, to.address || '')) {
            setTo(item);
        }
    }


    function setCursorPosition(pos, elem) {
        elem.focus();
        if (elem.setSelectionRange) elem.setSelectionRange(pos, pos);
        else if (elem.createTextRange) {
            var range = elem.createTextRange();
            range.collapse(true);
            range.moveEnd("character", pos);
            range.moveStart("character", pos);
            range.select()
        }
    }

    function mask(event) {
        var matrix = this.defaultValue,
            i = 0,
            def = matrix.replace(/\D/g, ""),
            val = this.value.replace(/\D/g, "");
        def.length >= val.length && (val = def);
        matrix = matrix.replace(/[_\d]/g, function (a) {
            return val.charAt(i++) || "_"
        });
        this.value = matrix;
        setPhone(matrix);
        i = matrix.lastIndexOf(val.substr(-1));
        i < matrix.length && matrix != this.defaultValue ? i++ : i = matrix.indexOf("_");
        setCursorPosition(i, this)
    }

    const createOrder = () => {
        const formData = new FormData;
        formData.append('test_field1', from.address)
        formData.append('source_lat', from.lat)
        formData.append('source_lon', from.lon)
        formData.append('test_field2', to.address)
        formData.append('destination_lat', to.lat)
        formData.append('destination_lon', to.lon)
        // formData.append('source_time', '24.03.2020 17:40')
        formData.append('source_time_iso8601', new Date().toISOString())
        formData.append('phone', phone)
        formData.append('test_field3', '')
        formData.append('comment', comment)
        formData.append('address_source', from.address)
        formData.append('address_destination', to.address)
        formData.append('name', props.location.state.order.name)
        formData.append('auth_token', 'AB890D4D-EB06-4DF9-9EDC-5493A9892FB1')

        //
        axios.post("https://websites.platform.taximaster.ru/v2/order", formData, {
            "headers": {
                "Sec-Fetch-Dest": "empty",
                "Sec-Fetch-Mod": "cors",
                "Sec-Fetch-Site": "same-origin",
                "X-Site-VtmId": "134",
                "X-Site-VtmGroupId": "56",
                "Accept": "*/*"
            },
        })
            .then(response => {
                openNotificationWithIcon('success')
                localStorage.setItem('phone', phone)
                orderApi.setStatus(props.location.state.order.id, 1);
                props.history.push(`/orders/${props.location.state.order.id}`)

            })
            .catch(error => {
                if (error.response) {
                    Object.values(error.response.data.errors).map(errorField => {
                        Object.values(errorField).map(errors => {
                            alert(errors.join('\n'))
                        })
                    })
                }

            })
    }

    const checkAddress = (itemAddress, prevAddress) => itemAddress === prevAddress;


    const openNotificationWithIcon = type => {
        notification[type]({
            message: '',
            description:
                '  ',
        });
    };



    return (
        <div className={"site-content"}>

            <Breadcrumb style={{margin: '16px 0'}}>
                <Breadcrumb.Item>
                    <Link to={"/"}>
                        
                    </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item>
                    <Link to={`/orders/${props.location.state.order.id}`}>
                        {props.location.state.order.name}
                    </Link>
                </Breadcrumb.Item>

                <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>

            <div className={"site-layout-content"}>
                <SearchAddress callback={selectFrom} item={from}/>

                <SearchAddress callback={selectTo} item={to}/>

                <Form.Item>
                    <Input placeholder={""}/>
                </Form.Item>

                <Form.Item>
                    <Input id={"phone"}  value={phone}
                           placeholder={"   +7(___)---_--_--"}/>

                    <a href="/#" onClick={e => {
                        e.preventDefault();
                        setPhone('+7(___)___-____')
                    }}></a>
                </Form.Item>

                <Form.Item>
                    <Input.TextArea value={comment} onChange={e => setComment(e.target.value)}
                                    placeholder={"  "}/>
                </Form.Item>

                <Button onClick={createOrder}> ( : {coast})</Button>

            </div>

        </div>
    )
}

export default Taxi;